* {
    box-sizing: border-box;
}

body {
    margin: 0px;
    padding: 0px;
}

/* nav */
nav {
    background-color: lightgrey;
    padding: 0 5px;
}

nav ul {
    display: inline-flex;
    justify-content: flex-start;
    padding: 5px;
    font-weight: bold;
    align-items: center;
    font-size: 2rem;
}

nav li {
    text-align: start;
    list-style-type: none;
    display: block;
    margin-right: 1.25rem;
}

nav a {
    text-decoration: none;
    padding: 1rem;
    font-size: 2rem;
}

nav a:link,nav a:visited {
    color: brown;
}

nav a:hover {
    text-decoration: underline;
    color: violet;
}

.nav-img {
    width: 40px;
}

/* Header */
.header-h1{
    padding-left: 1rem;
}

/* Main */
.main-h3{
    padding-left: 1rem;
    font-size: 2rem;
}

/*Router*/
.link-to{
    display: inline-flex;
    align-items: center;
} 

/*News*/
.CTA-btn{
    font-size:1.5rem;
    margin-bottom:10px;
    background-color:brown;
    color: white;
    border-radius: 1rem;
    padding: 10px;
}

.CTA-btn:hover{
    background-image: url('images/coffee02.jpg');
}

#News button{
    cursor: pointer;
}

/*About*/
.about-div{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 1px solid gray;
    margin: 1rem;
    font-size: 1.5rem;
}

.about-div:hover{
    cursor: pointer;
    background-color: black !important;
    color: white;
}

.about-img{
    border: 5px inset salmon;
    padding: 1rem;
}

/*Contact*/
.effect-font{
    font-size: 2rem;
    margin: 5px;
    padding: 5px;
}